import { descargarInstrucciones } from '../funciones/descargarInstrucciones'; 
import agitar from '../imagenes/agitar.png';
import jarabeInicial from '../imagenes/jarabe_inicial.png';
import dosificar from '../imagenes/dosificar.png';
import enrasar from '../imagenes/enrasar.png';
import grifo from '../imagenes/grifo.png';
import marca from '../imagenes/marca.png';
import mesa from '../imagenes/tabla.png';
import '../App.css';
import '../estilos/instrucciones.css';
import { Helmet } from 'react-helmet';

function Jarabes() {

    const handleClick = () => {
        descargarInstrucciones(("jarabes"));
        
    };
    
    return (
        <div id="container-principal"className="container">

            <Helmet>
                <title>Jarabes Antibiticos</title>
                <meta name="description" content="Guia de pasos para preparar y reconstituir Jarabes Antibiticos." />
                </Helmet>

            <div className="button-container">  
               
                    <button id="botonDescargar" className="button" onClick={handleClick}>Descargar Instrucciones de Uso</button>
              
            </div>

            <div id="container-instrucciones" className="instrucciones">   
                <h2 id="titulo-instrucciones">Instrucciones de Uso: Antibiticos Jarabe - Polvo para Reconstituir Va Oral</h2>
                <div className="instructions-content">
                    <br/>
                    <p id="titulo-pasos"><strong>PREPARACIN DE LA SUSPENSIN: </strong> 
                    </p>

                    <p><strong id="aling-izq">Paso 1:</strong> <img src={agitar} alt="agitar" className="instruction-icon" /><img src={jarabeInicial} alt="jarabe inicial" className="instruction-icon" /> Antes de abrir el frasco, <strong>Agite enrgicamente</strong> para que <strong id="azul">el polvo est suelto y no quede compactado en el fondo</strong>. 
                    </p>
                    
                    <p><strong id="aling-izq">Paso 2:</strong> <img src={grifo} alt="grifo" className="instruction-icon" /><img src={marca} alt="llenar hasta la marca" className="instruction-icon3" /> Abra el frasco y <strong>Agregue agua</strong> hasta que el <strong id="azul">nivel del agua est en la marca indicada</strong> en el envase.
                    <br />
                    <br/>
                     <strong id="azul">Use agua potable a temperatura ambiente</strong>. 
                    <br/>
                    <br/>
                    <strong id="azul">En caso de no tener</strong>, emplee el agua que utiliza habitualmente para el consumo. Se recomienda el uso de agua hervida (por no ms de 5 minutos) y enfriada a temperatura ambiente antes que agua embotellada. Si no tiene otra opcin que usar agua embotellada, asegrese de que tenga bajo contenido de sodio y electrolitos, ya que estos pueden alterar al medicamento. 
                    <br/>
                    <br /> <strong id="azul">NO usar agua caliente o tibia</strong> porque puede daar al antibitico. 
                    <br />
                    </p>
                    <p><strong id="aling-izq">Paso 3:</strong> <img src={agitar} alt="agitar" className="instruction-icon" /> <strong>Agite enrgicamente</strong> para que<strong id="azul"> todo el polvo se mezcle completamente con el agua</strong>. Asegrese que no quede en el fondo polvo sin mezclar.
                    </p>
                   
                    <p><strong id="aling-izq">Paso 4:</strong> <img src={mesa} alt="mesa" className="instruction-icon" /> <strong>Deje Reposar</strong> unos minutos el frasco en una <strong id="azul">superficie plana</strong> para que se disipe la espuma y la mezcla se asiente.
                    </p>

                    <p><strong id="aling-izq">Paso 5:</strong> <img src={enrasar} alt="enrasar" className="instruction-icon2" /> <strong>Enrasar</strong> agregando <strong id="azul">agua hasta la marca indicada en el envase</strong>. No se preocupe si queda algo de espuma; asegrese de que el agua alcance la marca, ignorando la espuma.
                    <br/>
                    <br/>
                     Para lograr un <strong id="azul">enrasado preciso</strong>, asegrese de que la marca del envase est a la altura de sus ojos al medir el nivel del agua.
                    </p>
                  
                    <p><strong id="aling-izq">Paso 6:</strong> <img src={agitar} alt="agitar" className="instruction-icon" /><img src={mesa} alt="mesa" className="instruction-icon" /> <strong id="azul"></strong> <strong>Agite otra vez enrgicamente</strong> el frasco despus de enrasarlo y <strong>djelo reposar</strong> unos minutos en una superficie plana. 
                    </p>
                    <br/>
                    <p><strong id="aling-izq">Paso 7:</strong>  <strong>Verifique que el agua llegue hasta la marca del envase</strong>. Si el <strong id="azul">agua est en la marca</strong>, el antibitico esta <strong id="azul">listo para usar</strong>.
                    </p>
                    <br/>
                    <p><strong id="aling-izq">Paso 8:</strong>  En caso de que el <strong id="azul">agua no alcance la marca</strong>, <strong>repita los Pasos</strong> a partir del proceso de enrasado del <strong>Paso 5.</strong> As se asegurar de que la suspensin tenga la concentracin adecuada.
                    </p>
                    <br/>
                    <br/>

                    <p id="titulo-pasos"><strong>ADMINISTRACIN: </strong> </p>
                    <p><strong id="aling-izq">Paso 1:</strong> <img src={agitar} alt="agitar" className="instruction-icon" /> <strong>Agite el frasco antes de administrar cada dosis oral</strong> para garantizar <strong id="azul"> una distribucin uniforme del antibitico</strong>.
                    </p>

                    <p><strong id="aling-izq">Paso 2:</strong> <img src={dosificar} alt="dosificar" className="instruction-icon3" /> <strong>Para tomar la dosis correcta,</strong>  utilice un vasito dosificador o una jeringa.  <strong id="azul">NO usar cucharas o cucharitas de cocina.</strong>
                    </p>
                    <br/>
                    <br/>
                   
                    <p id="titulo-pasos"><strong>ALMACENAMIENTO: </strong> </p>
                   
                    <p> <strong>Mantenga el antibitico fuera del alcance de los nios.</strong> 
                    <br/>
                    <br/>
                     <strong>Revise el prospecto o envase</strong> <strong id="azul">para saber cul es el tiempo de conservacin y condiciones de almacenamiento (heladera o temperatura ambiente) luego de que se agreg agua (suspensin reconstituida)</strong>, que pueden variar segn el antibitico y la marca comercial. 
                    </p>
                    <br/>

                    <p className="important">
                    <strong id="manito"> IMPORTANTE!: </strong> 
                    <br/>
                    <span id="manito"><strong id="azul">Es fundamental seguir las indicaciones mdicas y completar el tratamiento durante todos los das indicados, incluso si el nio parece sentirse mejor antes. Esto garantizar una recuperacin completa y ayudar a prevenir la resistencia a los antibiticos.
                    <br/>
                    <br/>
                     Cumplir con los horarios de las dosis.
                    <br/>
                    <br/>
                     Es recomendable anotar la fecha de preparacin en el envase del antibitico para garantizar su uso dentro del perodo recomendado despus de preparado con agua.
                    <br/>
                    <br/>
                     Lavarse o desinfectarse las manos antes de manipular el frasco.
                    <br/>
                    <br/>
                     Asegrese Siempre de Administrar el Medicamento Correcto al Nio. Siempre Verifique que el medicamento indicado por el mdico, incluyendo Nombre y Concentracin, sea igual a la informacin que aparece en la Etiqueta del Medicamento.</strong>
                   </span>
                   <br/>
                    <br/>  
                    </p>

                </div>
            </div>
           
        </div>
    );
}

export default Jarabes;