import '../App.css';
import '../estilos/menuQR.css';
import  {useAppContext}  from '../hooks/AppContext'; 
import Tarjeta from './Tarjeta';
import { Helmet } from 'react-helmet';

function MenuQR() {
  
  const qrImages = [
    { nombre: 'Instrucciones1', imagen: 'jarabe.png', grupo: 'Antibiticos Jarabe - Polvo para Reconstituir Va Oral', medicamentos: 'Amoxicilina - Amoxicilina/Clavulnico - Cefalexina - Claritromicina' },
    { nombre: 'Instrucciones2', imagen: 'inhalador.png', grupo: 'Aerosoles y Aerocmaras', medicamentos: 'Budesonide - Salbutamol' },
  ];

  const { isReadyForInstall, downloadApp } = useAppContext(); 

  return (
    <div id="container-menu" className="container"> {/* Contenedor principal */}

    <Helmet>
    <title>Instrucciones de Uso de Medicamentos - Farmacia HNRG</title>
    <meta name="description" content="Gua para Padres: Aprende a preparar Jarabes Antibiticos y usar Aerosoles y Aerocmaras - Farmacia del Hospital de Nios Ricardo Gutirrez (HNRG) | FarmaGuti" />
    </Helmet>

      <h2 className="titulo-menuQr">Haga Clic en el Botn para Ver las Instrucciones de Uso</h2>

      <div className="menuQr"> {/* Contenedor especfico del men QR */}
        {/* Mapear las imgenes QR */}
        {qrImages.map((item, index) => (
          <Tarjeta key={index} nombre={item.nombre} qrImage={item.imagen} grupo={item.grupo} medicamentos={item.medicamentos} />
        ))}
      </div>

      {isReadyForInstall && <div id="sombra">
        <h4 id="verde">Haga clic en el botn si desea descargar la pgina como una App</h4>
        <button className="button" id="botonInstalarPWA" onClick={downloadApp}>Instalar App </button>
      </div>}

    </div>
  );
}

export default MenuQR;
