// En tu componente JSX
import { descargarInstrucciones } from '../funciones/descargarInstrucciones'; 
import destapar from '../imagenes/destapar.png';
import agitar from '../imagenes/agitar.png';
import inhaladorParado from '../imagenes/inhaladorParado.png';
import aerosol_aerocamara from '../imagenes/aerosol.png';
import posicion from '../imagenes/sentado.png';
import mascara from '../imagenes/mascara.png';
import contento from '../imagenes/contento.png';
import aerocamara from '../imagenes/aerocamara.png';
import inhaladorApretado from '../imagenes/inhaladorApretado.png';
import '../App.css';
import '../estilos/instrucciones.css';
import { Helmet } from 'react-helmet';

function Aerosoles() {
   
    const handleClick = () => {
        descargarInstrucciones(("aerosoles"));
      
    };
    
    return (
        <div id="container-principal" className="container">

            <Helmet>
                <title>Aerosoles y Aerocmaras </title>
                <meta name="description" content="Guia de pasos para usar Aerosoles y Aerocmaras." />
                </Helmet>

            <div className="button-container">  
                
                    <button id="botonDescargar" className="button" onClick={handleClick}>Descargar Instrucciones de Uso</button>
                
            </div>
    
            <div id="container-instrucciones" className="instrucciones">   
                <h2 id="titulo-instrucciones">Instrucciones de Uso: Aerosoles y Aerocmaras</h2>
                <div className="instructions-content">
                    <p><strong id="aling-izq">Paso 1:</strong> <img src={destapar} alt="destapar" className="instruction-icon"/><strong id="azul">Retire la tapa de la boquilla del aerosol</strong>. Si usa por <strong id="azul">primera vez el aerosol o han pasado ms de 7 das sin usarlo</strong>, Agite el aerosol y realice 1 o 2 disparos de prueba al aire para asegurar su funcionamiento.</p>

                    <p><strong id="aling-izq">Paso 2:</strong> <img src={agitar} alt="agitar" className="instruction-icon" /><img src={inhaladorParado} alt="inhalador parado" className="instruction-icon" /><strong id="azul">Agitar el aerosol enrgicamente por 10 segundos</strong>.</p>

                    <p><strong id="aling-izq">Paso 3:</strong> <img src={aerosol_aerocamara} alt="aerosol_aerocamara" className="instruction-icon2" /> <strong id="azul">Con el aerosol hacia arriba</strong>, inserte la boquilla del aerosol en la aerocmara.</p>

                    <p><strong id="aling-izq">Paso 4:</strong> <img src={posicion} alt="posicion" className="instruction-icon3" /> El nio debe tener una posicin cmoda y con la espalda recta, <strong id="azul">preferentemente sentado y con el mentn ligeramente levantado</strong> para mantener despejada la va area.  Debe estar <strong id="azul">despierto, sin llorar y sin chupete</strong>.  <strong id="azul">En bebs,</strong> sintase con la espalda recta y colque al beb en su falda, asegrase de apoyar la cabeza y espalda del beb sobre su pecho para que pueda respirar comoda y fcilmente.</p>

                    <p><strong id="aling-izq">Paso 5:</strong> <img src={mascara} alt="mascara" className="instruction-icon" /> <strong id="azul">Coloque la mscara de la aerocmara</strong> cubriendo nariz y boca, sellando con la cara. </p>

                    <p><strong id="aling-izq">Paso 6:</strong> <img src={inhaladorApretado} alt="apretar inhalador" className="instruction-icon" /> <strong id="azul">Apriete el aerosol 1 sola vez (haga 1 solo disparo)</strong> y <strong>Cuente hasta 10 sin retirar la mscara de la cara</strong> mientras el nio respira lento y profundo por la boca.</p>

                    <p><strong id="aling-izq">Paso 7:</strong> <img src={contento} alt="contento" className="instruction-icon" /><strong id="azul">Retire</strong> la mscara de la cara y el aerosol de la aerocmara.</p>
                    <br/>

                    <p><strong id="aling-izq">Paso 8:</strong>  Repita los <strong>Pasos 2 a 7</strong> segn la cantidad de disparos que le indique el mdico.  <strong id="azul">Espere al menos 1 minuto</strong> entre cada disparo.</p>
                    <br/>

                    <p><strong id="aling-izq">Paso 9:</strong>  <strong id="azul">Al finalizar la cantidad de disparos indicados por el mdico</strong> colocar la tapa en la boquilla del aerosol.</p>
                    <br/>

                    <p><strong id="aling-izq">Paso 10:</strong>  <strong id="azul">Enjuagar la boca del nio</strong> con agua potable, evitando en lo posible que trague el agua.</p>

                    <br/>

                    <p><strong id="aling-izq">Paso 11:</strong>  <strong id="azul">Guardar el aerosol y la aerocmara</strong> en un lugar fresco y seco y alejado del alcance de los nios.</p>
                    <br/>
                    
                    <p className="important">
                        <br/>    
                        <strong id="manito"> Cunto tiempo puedo usar el Aerosol garantizando que tenga Medicamento? </strong> 
                        <br/>
                        <strong id="azul">El Aerosol sigue haciendo disparos incluso despus de que el Medicamento se haya agotado; en lugar de Medicamento, se liberan solo excipientes. </strong> <strong> Para calcular cunto tiempo durar el Aerosol con Medicamento, siga estos pasos:</strong>
                        <br/>
                        <br/> 
                        <strong >1. </strong><strong id="azul">Anote la fecha</strong> en que comienza a usar el aerosol.
                        <br/>
                        <br/>
                        <strong>2. </strong>Cuente la <strong id="azul" >cantidad de disparos que usa en un da</strong>, por ejemplo 4.
                        <br/>
                        <br/>
                        <strong>3. </strong>Mire en la etiqueta <strong id="azul">cuntas dosis tiene el aerosol</strong>, normalmente 200.
                        <br/>
                        <br/>
                        <strong>4. </strong>Divida <strong id="azul" >la cantidad total de dosis del aerosol entre los disparos que usa en un da</strong> (200 / 4).
                        <br/>
                        <br/>
                        <strong>5. </strong>Esto dar una <strong id="azul">estimacin de cuntos das durar el aerosol con medicamento despus de comenzar a usarlo</strong>, en el ejemplo alrededor de <strong id="azul">50 das</strong>.
                        <br/>
                        <br/>
                        <strong>6. </strong> <strong id="azul">Si no utiliza el aerosol regularmente</strong>, es importante <strong id="azul">anotar los disparos realizados cada vez que lo usa</strong> para saber cundo necesitar un reemplazo.

                        <br/>
                        <br/>
                                        
                    </p>

                    <p className="important">
                    <img src={aerocamara} alt="aerocamara" className="instruction-icon1" />
                    <strong id="manito"> IMPORTANTE!: LAVE la Aerocmara cada 7 a 15 das, siguiendo estos pasos:</strong> 
                    <br/>
                    <strong>1. Desarme la Aerocmara:</strong> Retire las piezas <strong id="azul">sin quitar la vlvula del portavlvula</strong> y sumrjalas en un recipiente con <strong id="azul">agua potable tibia</strong> (<strong id="azul">NO caliente</strong>) y  <strong id="azul">aproximadamente 5 gotas de detergente por litro de agua</strong> (para que se forme una ligera espuma en la superficie del agua)<strong id="azul"> durante 15 minutos</strong>. <strong id="azul">Remueva suavemente</strong> las piezas en el recipiente.
                    <br/>
                    <br/>
                    <strong>2. Enjuague:</strong> Vuelva a sumergir las piezas en un recipiente con <strong id="azul">agua potable tibia y sin detergente</strong>  y <strong id="azul">remuvalas suavemente unos minutos</strong> para enjuagarlas.
                    <br/>
                    <br/>
                    <strong>3. Secado:</strong> Sacuda las piezas para eliminar el exceso de agua y djelas <strong id="azul">secar al aire en posicin vertical sobre servilletas de papel o un pao limpio.</strong> Asegrese de que las piezas estn completamente secas antes de rearmar la aerocmara.
                    <br/>
                    <br/>
                    
                    <span id="manito"> <strong id="azul">NO use agua directamente de la canilla. Siempre use un recipiente para sumergir las piezas.</strong></span>
                    <br/>
                    <span id="manito"> <strong id="azul">NO FROTE con esponja NI SEQUE con papel o pao las piezas de la aerocmara.</strong> </span>
                    <br/>
                    
                </p>

                <p className="important">
                    
                    <br/>
                    <strong id="manito" > Siempre antes de manipular el Aerosol y la Aerocmara lavarse o desinfectarse las manos.</strong>
                    <br/>
                    <strong id="manito"> NO COMPARTIR AEROCMARAS.</strong>
                    <br/>
                    <strong id="manito"> NO USAR AEROCMARAS ROTAS O SIN VLVULA.</strong>
                    <br/>
                    <span id="manito"><strong id="azul"> Asegrese Siempre de Administrar el Medicamento Correcto al Nio. Siempre Verifique que el medicamento indicado por el mdico, incluyendo Nombre y Concentracin, sea igual a la informacin que aparece en la Etiqueta del Medicamento.</strong></span>
                    <br/>
                    <br/>
                </p>

                

                    
                    
                </div>
            </div>
        </div>
    );
}    

export default Aerosoles;